.list-work-container{
    display: flex;
    flex-direction: column;
    flex: 1;
    align-items: center;
    width: min(400px, 100%);
    height: 500px;
    margin: 60px;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 0 3px 13px 3px rgb( 0 0 0 / 9%);
    overflow: auto;
}
.form-container{
    display: flex;
    justify-content: center;
    width: 100%;
    align-items: center;
}
.content-input{
    width: 60%;
}
.content-input input{
    width: min(300px, 100%);
    border: none;
    outline: none;
    border-bottom: 1px solid #111;
    font-size: 15px;
}

.list-work-title{
    text-align: center;
    margin: 0px;
    padding: 20px;
}


.works-container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    
}

.work{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 50px;
    margin: 5px 20px;
    background-color: #111;
    border-radius: 15px;
    padding: 10px;
    color: #ffffff;
    box-shadow: 0 3px 13px 3px rgb( 0 0 0 / 9%);
    user-select: none;
    transition: all .5s;
}
.work-content{
    margin: 0;
}

.work-on{
    background-color: #fff;
    user-select: none;
    color: #111;
    text-decoration: line-through;
    transition: all .5s;
}
.work h1{
    outline: none;
    user-select: none;
    font-size: 15px;
    flex: 4;
}

.delete{
    width: 30px;
    border: none;
    outline: none;
    background-color: transparent;
    color: #f23333;
    font-size: 20px;
    cursor: pointer;

}

.save{
    width: 30px;
    border: none;
    outline: none;
    background-color: transparent;
    color: #225333;
    font-size: 20px;
    transition: all .5s;

}

.save-on{
    color: #22d333;
    cursor: pointer;
    transition: all .5s;
}

.btn-add{
    font-size: 25px;
    background-color: transparent;
    border: none;
    outline: none;
    width: 50px;
    cursor: pointer;
}