.nav-container{
  position: fixed;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #333333;
}


.nav-logo-coffe{
  display: flex;
  align-items: center;
  height: 100%;
  font-size: 30px;
  margin: 8px;
  margin-left: 10px;
  color: #fff;
}

.menu{
  transform: rotate(-90deg);
  transition: all 0.5s;
}

.menu-on{
  transform: rotate(0);
  transition: all 0.5s;
}

.nav-menu{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  font-size: 30px;
  margin-right: 30px;
  color: #fff;
  z-index: 300;
  text-align: end;
  cursor: pointer;
}

.icon-nav{
  margin-left: 20px;
  color: #fff;
}
.options-nav{
  position: absolute;
  margin-top: 10px;
  height: 98vh;
  right: -250px;
  width: 150px;
  background-color: #333;
  transition: all 0.5s;
  z-index: 100;
}

.options-nav-on{
  right: 0;
  transition: all 0.5s;
  z-index: 100;
}

.options-js{
  position: absolute;
  margin-top: 10px;
  height: 98vh;
  right: -250px;
  width: 150px;
  background-color: #222;
  transition: all 0.5s;
  z-index: 100;
}

.options-js-on{
  right: 150px;
  transition: all 0.5s;
  z-index: 80;
}

.options-rt{
  position: absolute;
  margin-top: 10px;
  height: 98vh;
  right: -250px;
  width: 150px;
  background-color: #222;
  transition: all 0.5s;
  z-index: 80;
}

.options-rt-on{
  right: 150px;
  transition: all 0.5s;
  z-index: 100;
}
