.boton-contenedor{
    display: flex;
    justify-content: center;
    height: 50px;
    width: 50px;
    flex: 1 1;
    border-radius: 20px;
    background-color: #fff;
    color: black;
    font-size: 30px;
    font-weight: bold;
    align-items: center;
    cursor: pointer;
    user-select: none;
    margin: 5px;
}

.operador{
    background-color: #222;
    color: #fff;
}

.boton-contenedor:hover{
    background-color: #d1d1f1;
}

.operador:hover{
    background-color: #333;
}
.btnClear{
    background-color: #fa3344;
    width: 100%;
    height: 80px;
    display: flex;
    border-radius: 20px;
    color: #111;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    user-select: none;
}

.btnClear:hover{
    background-color: #ff2324;
    cursor: pointer;
}

.igual{
    background-color: aqua;
}