.calculdora-container{
    display: flex;
    background: rgb( 0 0 0 / 60%);
    padding-top: 70px;
    height: 100vh;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .title-container{
    text-align: center;
    width: 100%;
  }
  
  .title{
    color: #f3f3f3;
    margin: 0px;
    padding: 20px;
  }
  
  .calculator{
    width: 300px;
    height: 450px;
    background:rgb( 0 0 0 / 30%);
    border-radius: 40px;
    padding: 20px;
  }
  
  .fila{
    margin: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  