.skills-container-div{
    margin-top: 100px;
    display: flex;
    width: 100%;
}

.container-skills-prim{
    display: flex;
    height: 200px;
    width: min(90%, 800px);
    background-color: #fff;
    box-shadow: 0 3px 13px 3px rgb( 0 0 0 / 9%);
    border-radius: 20px;
    margin: auto;
}
.container-skills{
    display: flex;
    height: 200px;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 3px 13px 3px rgb( 0 0 0 / 9%);
    overflow: auto;
    border-radius: 20px;
}

.skill{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px;
    margin-left: 20px;
    box-shadow: 0 3px 13px 3px rgb( 0 0 0 / 20%);
    padding: 10px;
    border-radius: 15px;
}

.skill-image{
    height: 100px;
    margin: 10px;
    width: 80px;
    background-position: center ;
}

.skill-image img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.delete-skill{
    border: none;
    outline: none;
    background-color: transparent;
    border: 2px solid #111;
    border-radius: 10px;
    color: #111;
    padding: 0px 10px;
    font-size: 15px;
    user-select: none;
}

.delete-skill:hover{
    cursor: pointer;
    background: #111;
    color: #fff;
    transition: all .5s;
}

.form-container-skills{
    position: absolute;
    display: none;
    height: 150px;
    width: 250px;
    right: 100px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: transparent;
    opacity: 0;
    transition: all .5s;
    animation: desaparecer 1s ;
}

.form-container-skills-on{
    opacity: 1;
    display: flex;
    height: 150px;
    width: 250px;
    transition: all .5s;
    animation: aparecer 1s ;
}

@keyframes aparecer {
    0%{
        transform: translateX(100px);
        opacity: 0;
    }100%{
        transform: translateX(0px);
        opacity: 1;
    }
}

@keyframes desaparecer {
    0%{
        transform: translateX(0);
        opacity: 1;
    }100%{
        transform: translateX(100px);
        opacity: 0;
    }
}


.form-skill{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    color: #fff;
    opacity: 0;
    transition: all .5s;
}

.form-skill-on{
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #111;
    border-radius: 20px;
    color: #fff;
    opacity: 1;
    transition: all .5s;
}

.skill-image-form {
    border: none;
    outline: none;
    margin: 10px;
    padding: 2px 5px;
    background-color: transparent;
    border: 2px solid #fff;
    border-radius: 10px;
    color: #fff;
}
.skill-image-form:hover {
    border: 2px solid #fff;
    background-color: #fff;
    border-radius: 10px;
    color: #111;
    cursor: pointer;
}
.skill-image-forz {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
    }

.skill-name{
    margin: 10px;
    border: none;
    outline: none;
    background-color: transparent;
    border-bottom: 1px solid #fff;
    font-size: 15px;
    color: #fff;
    font-weight: bold;
}
.skill-name::placeholder{
    font-size: 15px;
    color: #f3f3f3;
}

.btn-add-skill {
    border: none;
    outline: none;
    margin: 10px;
    padding: 2px 5px;
    background-color: #111;
    border: 2px solid #fff;
    border-radius: 10px;
    color: #fff;
}
.btn-add-skill:hover {
    border: 2px solid #fff;
    background-color: #fff;
    border-radius: 10px;
    color: #111;
    cursor: pointer;
}

.add-skill{
    display: flex;
    margin: 10px;
    background-color: #f3f3f3;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    color: #333;
    width: 50px;
    border-radius: 20px;
    cursor: pointer;
}
