.waether-container{
    text-align: center;
    flex: 2;
    width: 50%;
    height: 100%;
    background-color: #fff;
    margin-right: 50px;
    border-radius: 20px;
    padding: 20px;
    box-shadow: 0 3px 13px 3px rgb( 0 0 0 / 9%);
}
.container-card{
    height: 100%;
    width: 100%;
}

.container-card-temp{
    display: flex;
    height: 100%;
    width: 100%;
}
.temp-description{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 2;
    height: 100%;
    width: 100%;
}


.text{
    margin: 2px;
}
.container-form-temp{
    width: 100%;
    display: flex;
    z-index: 10;
    align-items: center;
}

.form-contro{
    width: 70%;
    height: 38px;
    border-radius: 10px;
    border: 1px solid #111;
    outline: none;
}

.form-contro:focus{
    box-shadow: 0 3px 13px 3px rgb( 0 0 0 / 20%);
}

.btn-primari{
    background-color: #111;
    color: #fff;
    outline: #333;
    z-index: 1;
    margin-left: 10px;
}
.btn-primari:hover{
    background-color: #fff;
    color: #111;
    outline: #333;
}

.btn-primari:active{
    background-color: #111;
    color: #fff;
    outline: 1px solid #333;
    border: 1px solid #333;
    box-shadow: 0 3px 13px 3px rgb( 0 0 0 / 9%);
}
.temp-temps{
    text-align: center;
    padding-top: 30px;
}

.card-text{
    font-size: 15px;
}