.btn-nav{
    height: 50px;
    font-size: 20px;
    display: flex;
    align-items: center;
    padding-left: 10px;
    margin-bottom: 5px;
    font-weight: bold;
}
.relleno{
    background-color: #111;
    color: #fff;
}

.javascript-btn{
    color: #fff;
    border-bottom: 2px solid #fff242;
    margin: 5px;
    cursor: pointer;
    user-select: none;
}
.javascript-btn-on{
    background:  #fff242;
    color: #111
}

.react-btn{
    color: #fff;
    border-bottom: 2px solid #21a2ff;
    margin: 5px;
    cursor: pointer;
    user-select: none;
}

.react-btn-on{
    background:  #21b2ff;
    color: #111
}

.javascript-btn:hover{
    background:  #fff242;
    color: #111
}

.react-btn:hover{
    background:  #21b2ff;
    color: #111
}

.btn-option{
    color: #fff;
    border-bottom: 2px solid #ffff;
    margin: 5px;
    cursor: pointer;
    user-select: none;
}

.link{
    text-decoration: none;
}

.btn-option:hover{
    background-color: #fff;
    color: #111;
}
