.one-note-container{
    display: flex;
    padding-top: 50px;
    width: 100%;
    height: 100vh;
}

.add-note-container{
    display: flex;
    position: absolute;
    width: 100%;
    height: 92.6%;
    background: rgba( 0 0 0 / 30%);
    z-index: 200;
}

.add-note-container-on{
    display: flex;
    position: absolute;
    width: 100%;
    height: 92.6%;
    z-index: 200;
    overflow: auto;
    background: rgba( 0 0 0 / 30%);
    animation: see 1s;
}

@keyframes see {
    0%{
        opacity: 0;
    }100%{
        opacity: 1;
    }
}

.form-conatiner{
    margin: auto;
    width: 50%;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: 0px 3px 13px 3px rgba(0 0 0 /9%);
}

#form{
    display: flex;
    flex-direction: column;
    padding: 20px;
}

#title{
    border: none;
    outline: none;
    padding: 2px 15px;
    font-size: 20px;
}

#title:focus{
    border-bottom: 1px solid #111;
    animation: input-animation 1s;
}

@keyframes input-animation {
    0%{
        width: 10%;
    }100%{
        width: 100%;
    }
}

#content{
    border: none;
    outline: none;
    padding: 10px 40px;
    font-size: 20px;
}

#file{
    width: 0px;
    height: 0px;
}

.card-note{
    word-wrap: break-word;
}
.card-note-title{
    font-size: 25px;
}

.options-add-note{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.closed{
    cursor: pointer;
    position: relative;
    right: 0;
    margin-right: 10px;
    border: none;
    outline: none;
    background: transparent;
    font-size: 40px;
}

.save-note{
    width: fit-content;
    margin-left: 10px;
    outline: none;
    border: none;
    color: #111;
    font-size: 18px;
}

.save-note:hover{
    background-color: #111;
    color: #fff;
}

.insert{
    cursor: pointer;
    font-size: 25px;
    margin-left: 15px;
}

.cards-notes-container{
    flex: 2;
}
.options-card-notes{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 40px;
    border-bottom: 1px solid #333;
    box-shadow: 0px 3px 13px 3px rgba(0 0 0 /9%);
}

.option-add-note{
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 25px;
    margin-right: 10px;
}



.options-read-notes{
    height: 40px;
    border-bottom: 1px solid #333;
    box-shadow: 0px 3px 13px 3px rgba(0 0 0 /9%);
}

.notes-container{
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-auto-rows: 150px;
    gap: 10px;
    height: 92.6%;
    overflow: auto;
}

.card-grid{
    border-radius: 20px;
    min-width: 200px;
    min-height: 150px;
    padding: 10px;
    cursor: pointer;
    border: 5px solid #fff;
    box-shadow: 0px 3px 13px 3px rgba(0 0 0 /9%);
    overflow: hidden;
}

.alert-note{
    height: 92.6%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.alert-note h1{
    padding-bottom: 150px;
}


.card-grid:hover{
    color: #777;
    background: rgba(0 0 0 /20%);
    transition: all 1s;
    border: 5px solid rgba(0 0 0 /1%);
    box-shadow: 0px 3px 13px 3px rgba(0 0 0 /20%);
}

.card-note{
    word-wrap: break-word;
    width: 100%;
    height: 100%;
}
.card-note-title{
    font-size: 25px;
}

.btn-card-read-option-delete{
    position: relative;
    margin-right: 5px;
    top: -175px;
    right: -80px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 23px;
    transition: all .5s;
    opacity: 0;
}

.btn-card-read-option-see-complete{
    position: relative;
    top: -175px;
    right: -75px;
    margin-left: 20px;
    margin-right: 5px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 25px;
    transition: all .5s;
    opacity: 0;

}
.logo-card-read{
    position: relative;
    cursor: pointer;
    top: -155px;
    left: -20px;
    font-size: 80px;
    color: #333;
    margin-left: 30px;
    opacity: 0;
}

.btn-card-read-option-delete:hover{
    color: #f32323;
}

.btn-card-read-option-see-complete:hover{
    color: #2365da;
}


.card-note:hover > .btn-card-read-option-delete{
    transition: all .5s;
    opacity: 1;
    animation: aparecer-options-card .5s ;
}

.card-note:hover> .btn-card-read-option-see-complete{
    transition: all .5s;
    opacity: 1;
    animation: aparecer-options-card .7s ;
}

.card-note:hover> .logo-card-read{
    transition: all .5s;
    opacity: 1;
    animation: aparecer-options-logo .7s ;
    color: #111;
}

.card-note-date{
    width: 100%;
    text-align: end;
    margin-top: 40px;
}


.options-read-notes{
    padding-right: 20px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.btn-read-option-delete{
    background: transparent;
    border: none;
    outline: none;
    font-size: 23px;
    margin-right: 5px;
}

.btn-read-option-delete:hover{
    color: #f32323;
}

.btn-read-option-see-complete{
    background: transparent;
    border: none;
    outline: none;
    font-size: 23px;
    margin-right: 5px;
}

.btn-read-option-see-complete:hover{
    color: #2365da;
}

.btn-save-edit{
    background: transparent;
    border: none;
    outline: none;
    font-size: 23px;
    margin-right: 5px;
}
.btn-save-edit:hover{
    color: #44af22;
}

@keyframes aparecer-options-card {
    0%{
        opacity: 0;
        transform: translateX(100px);
    }100%{
        transform: translateX(0px);        
        opacity: 1;
    }
}

@keyframes aparecer-options-logo {
    0%{
        opacity: 0;
        transform: translateY(100px);
    }100%{
        transform: translateX(0px);        
        opacity: 1;
    }
}

.read-note-screen-complete{
    position: absolute;
    margin: 10px;
    width: 98%;
    height: 90%;
    background-color: #fff;
    border-radius: 20px;
    z-index: 300;
    overflow: auto;
    box-shadow: 0px 3px 15px 20px rgba(0 0 0 /30%);
    animation: screenCompleteA .5s;
}

@keyframes screenCompleteA {
    0%{
        opacity: 0;
        transform: translateY(-500px);
    }100%{
        opacity: 1;
        transform: translateY(0);
    }
}

.read-note-screen-complete h1{
    margin-left: 40px;
    font-size: 50px;
    border-bottom: 1px solid #111;
    width: fit-content;
    outline: none;
    animation: titleScreenComplete 1s;
}
@keyframes titleScreenComplete {
    0%{
        transform: translateX(-500px);
    }100%{
        transform: translateX(0);
    }
}

.read-note-screen-complete p{
    padding-left: 40px;
    outline: none;
    font-size: 30px;
    animation: textScreenComplete 1s;
}

@keyframes textScreenComplete {
    0%{
        transform: translateX(-700px);
    }100%{
        transform: translateX(0);
    }
}
.read-note-screen-complete-options{
    display: flex;
    justify-content: flex-end;
    border-bottom: 1px solid #111;
}

.btn-close-screen-complete{
    background: transparent;
    border: none;
    outline: none;
    font-size: 23px;
    margin-right: 5px;
}

.read-notes{
    flex: 1;
    width: min(400px, 100%);
}

.container-read-note{
    width: 100%;
    word-wrap: break-word;
    overflow: auto;
    height: 92.6%;
}

.read-title{
    border-bottom: 1px solid #111;
    margin: 0px 5px;
}

.read-content{
    padding: 5px 15px;
}

.img-content{
    width: 50%;
    height: 20%;
    display: flex;
}

.img-content:hover > .redimencionar{
    opacity: 1;
    transition: all 1s;
}

.read-note-screen-complete-img{
    margin-left: 30px;
    width: 50%;
    height: 70%;
}

.img-content-form{
    display: flex;
    width: 50%;
    height: 25%;
}

.img-content-form:hover .redimencionar{
    opacity: 1;
}
.img-form{
    width: 100%;
    height: 100%;
}

.img-read{
    width: 100%;
    height: 100%;
}

.title-app{
    height: 92.6%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.title-app h1{
    font-size: 80px;
}
#editFileChange{
    width: 0px;
}
.change-img{
    cursor: pointer;
    font-size: 25px;
    margin-right: 10px;
}

.redimencionar{
    position: relative;
    opacity: 0;
    background-color: transparent;
    outline: none;
    border: none;
    font-size: 25px;
    align-self: flex-end;
    left: -20px;
    top: 15px;
}
